@import '../../commons/styles/colors.scss';
@import '../../commons/styles/breakpoints.scss';

.cmp-navigation {
    .cmp-currencySelector {
        display: flex;
    }

    &__list {
        display: none;
        background-color: $color-light-black;

        @include medium {
            display: flex;
            padding: 20px 30px;
        }

        &--link {
            margin-right: 20px;

            a {
                color: $color-white;
            }
        }
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-basis: 100%;
        height: 50px;
        padding: 0 20px;
        background-color: $color-light-black;

        @include medium {
            display: none;
        }

        &__burger {
            cursor: pointer;
            font-size: 25px;
            display: flex;


            @include medium {
                display: none;
            }
        }
    }

    &__wrapper {
        display: flex;
        position: fixed;
        height: 100%;
        width: 100%;
        top: 0;
        z-index: 10;
        left: 100%;
        background-color: $color-light-black;
        transition: left 0.3s ease 0s;

        @include medium {
            display: none;
        }

        &__content {
            display: flex;
            flex-wrap: wrap;
            flex-basis: 100%;
            height: fit-content;

            &__header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                flex-basis: 100%;
                height: 50px;
                padding: 0 20px;
                background-color: $color-lighter-black;

                &--close {
                    cursor: pointer;
                    font-size: 40px;
                    display: flex;
                }
            }

            &__list {
                display: flex;
                flex-basis: 100%;
                padding: 0 20px;
                flex-wrap: wrap;
                justify-content: center;

                &--link {
                    margin-right: 20px;
                    flex-basis: 100%;
                    text-align: center;
                    font-size: 30px;
                    margin: 20px 0;

                    a {
                        color: $color-white;
                    }
                }
            }
        }

        &.open {
            left: 0;
        }
    }
}