// breakpoints //
$extra-small: 420px;
$small: 768px;
$medium: 1024px;
$large: 1400px;

// screens //
@mixin extra-small {
    @media (max-width: $extra-small) {
        @content;
    }
}

@mixin small {
    @media (min-width: $small) {
        @content;
    }
}

@mixin medium {
    @media (min-width: $medium) {
        @content;
    }
}

@mixin large {
    @media (min-width: $large) {
        @content;
    }
}